.nauth-container {
	max-width: 1200px;
	margin: 0 auto;
	height: 100%;

	@media (max-width: 1200px) {
		max-width: 525px;
	}
	@media (max-width: 479.98px) {
		max-width: 470px;
	}
}
.auth-container {
	max-width: calc(100vw - (54px * 2));
	margin: 0 auto;
	height: 100%;

	@media (max-width: 479.98px) {
		max-width: 352px;
	}

	@media (max-width: 357px) {
		max-width: 310px;
	}
}

.home-container {
	width: 800px;
	height: fit-content;
	transition: all 450ms ease-in-out;

	@media (max-width: 1124px) {
		width: 663px;
	}
	@media (max-width: 991.98px) {
		width: 558px;
	}
	@media (max-width: 767.98px) {
		width: 590px;
	}
	@media (max-width: 696.98px) {
		width: calc(100vw - (54px * 2));
	}
	@media (max-width: 479.98px) {
		width: 351px;
	}
	@media (max-width: 357px) {
		width: 310px;
	}

	&__se {
		@media (max-width: 880.98px) {
			width: 462px;
		}
		@media (max-width: 767.98px) {
			width: 590px;
		}
		@media (max-width: 696.98px) {
			width: calc(100vw - (54px * 2));
		}
		@media (max-width: 479.98px) {
			width: 351px;
		}
		@media (max-width: 357px) {
			width: 310px;
		}
	}
}
